import {CONTACTLIST,CONTACTDETAIL} from "../../utils/api.js"
export default {
	data() {
		return {
			listData: ''
		}
	},
	onShow() {
		// 通讯录列表
		this.getAddressBook()
	},
	onLoad() {
		// 通讯录列表
		// this.getAddressBook()
		uni.showToast({
			title:'加载中...',
			icon:'none',
			mask:true
		})
	},
	onPullDownRefresh() {
		this.getAddressBook()
	},
	methods: {
		formateName(value) {
			var result;
			if(value.length>3){
				result = value.substring(0,3)+ '...'
			} else {
				result = value
			}
			return result
		},
		// 通讯录详情
		userDetail(tel,head) {
			const detail = JSON.stringify({mobile:tel,head:head})
			uni.navigateTo({
				url: 'ContactDetail/ContactDetail?detail='+detail
			})
		},
		getAddressBook() {
			const params = {}
			this.$request.url_request(CONTACTLIST,params,"GET",res =>{
				this.listData = JSON.parse(res.data).data
				uni.hideToast()
				uni.stopPullDownRefresh()
			},err =>{})
		},
		gotoEditorialOpen() {
			uni.navigateTo({
				url:"AddressBook/AddressBook"
			})
		}
	}
}
